.slick-prev,
.slick-next
{
    font-size: 0;
    line-height: 0;

    position: fixed;
    top: 50%;

    display: block;

    width: 20px;
    height: 20px;
    padding: 0;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);

    cursor: pointer;

    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}
.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus
{
    color: transparent;
    outline: none;
}
.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before
{
    opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before
{
    opacity: .25;
}

.slick-prev:before,
.slick-next:before
{
    font-family: 'slick';
    font-size: 20px;
    line-height: 1;

    opacity: .75;
    color: white;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-prev
{
    left: -25px;
}
[dir='rtl'] .slick-prev
{
    right: -25px;
    left: auto;
}
.slick-prev:before
{
    content: '←';
}
[dir='rtl'] .slick-prev:before
{
    content: '→';
}

.slick-next
{
    right: -25px;
}
[dir='rtl'] .slick-next
{
    right: auto;
    left: -25px;
}
.slick-next:before
{
    content: '→';
}
[dir='rtl'] .slick-next:before
{
    content: '←';
}

/* Dots */
.slick-dotted.slick-slider
{
    margin-bottom: 30px;
}

.slick-dots
{
    position: absolute;
    bottom: -25px;

    display: block;

    width: 100%;
    padding: 0;
    margin: 0;

    list-style: none;

    text-align: center;
}
.slick-dots li
{
    position: relative;

    display: inline-block;

    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;

    cursor: pointer;
}
.slick-dots li button
{
    font-size: 0;
    line-height: 0;

    display: block;

    width: 20px;
    height: 20px;
    padding: 5px;

    cursor: pointer;

    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}
.slick-dots li button:hover,
.slick-dots li button:focus
{
    outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before
{
    opacity: 1;
}
.slick-dots li button:before
{
    font-family: 'slick';
    font-size: 6px;
    line-height: 20px;

    position: absolute;
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;

    content: '•';
    text-align: center;

    opacity: .25;
    color: black;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before
{
    opacity: .75;
    color: black;
}

.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
    display: flex;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    /* height: 100%; */
    min-height: 1px;
    
}
.slick-slide > div, .slick-slide > div> div {
    height: 100%;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,400;1,900&display=swap');

*,
*::before,
*::after,
*:focus {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    outline: none;
}

body {
    margin: 0px;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    line-height: 16px;
    min-height: 100vh;
    height: auto;
}

.wrapper {
    overflow: hidden;
    min-height: 100vh;
    height: 100%;
    background: url(../img/bg1.jpg) no-repeat center;
    background-size: cover;
    background-attachment: fixed;
    min-width: 960px;
    padding-top: 60px;
}

a {
    color: #67779A;
}

.rouble {
    font-family: 'rouble';
}
input, textarea {
    font-family: 'Roboto', sans-serif;
}
img {
    max-width: 100%;
}

.-flex {
    display: flex;
}

.-center {
    align-items: center;
}

.-start {
    align-items: flex-start;
}

.-end {
    align-items: flex-end;
}

.-wrap {
    flex-wrap: wrap;
}

.-justify-between {
    justify-content: space-between;
}

.pt-0 {
    padding: 0px !important;
}
.mt-12 {
    margin-top: 12px !important;
}

.btn {
    display: table;
    padding: 8px 24px;
    background: #9FBAF4;
    border-radius: 5px;
    font-size: 12px;
    color: #fff;
    text-decoration: none;
    transition: all .2s;
    white-space: nowrap;
    cursor: pointer;

    @media(max-width:1100px) {
        padding: 10px 14px;
    }

    &:hover {
        background: #6091F8;
    }

    &:active {
        background: #4C74C7;
    }

    &.btn-pink {
        background: #C185AD;
        padding: 10px 24px;
        border-radius: 3px;
        font-size: 14px;
        line-height: 16px;
        padding: 10px 14px;

        @media(max-width:1100px) {
            padding: 10px 14px;
        }

        &:hover {
            background: #ad85a0;
        }
    }

    &.btn-fillblue {
        background: #6091F8;
        padding: 10px 24px;
        border-radius: 3px;
        font-size: 14px;
        line-height: 16px;
        color:#fff;
        @media(max-width:1100px) {
            padding: 10px 14px;
        }

        &:hover {
            background: #9FBAF4;
        }
    }

    &.btn-red {
        background: #FF0000;
        padding: 10px 24px;
        border-radius: 3px;
        font-size: 14px;
        line-height: 16px;

        @media(max-width:1100px) {
            padding: 10px 14px;
        }

        &:hover {
            background: #e73434;
        }
    }
}

input,
button,
textarea,
label {
    outline: none;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    border: none;

    &:focus {
        outline: none;
        -webkit-appearance: none;
    }

    &:active {
        outline: none;
        -webkit-appearance: none;
    }

    &:hover {
        outline: none;
        -webkit-appearance: none;
    }
}

input {
    background: #FFFFFF;
    border: 1px solid #DEE4F2;
    height: 30px;
    border-radius: 3px;
    padding: 0px 10px;
}

.header {
    padding: 19px 40px 13px 40px;
    background: #fff;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    border-bottom: 1px solid #f2f2f2;
    z-index: 12;
    @media (max-width:1070px) {
        padding: 19px 10px 13px 20px;
    }

    &__name {
        margin-right: 26px;
        color: #08235C;
        display: inline-block;
    }
}

.main-nav {
    width: 240px;
    background: rgba(233, 240, 255, 1);
    min-height: calc(100vh - 64px);
    height: auto;
    top: 64px;
    left: 0px;
    font-size: 16px;
    line-height: 19px;
    color: #67779A;
    position: fixed;
    left: 0px;
    z-index: 9;
    @media (max-width:1070px) {
        width: 210px;
    }

    ul {
        margin: 0px;
        padding: 0px;
        padding-top: 40px;
        padding-right: 40px;

        @media (max-width:1070px) {
            padding-right: 10px;
            padding-top: 20px;
        }

        li {
            display: block;
            margin-bottom: 5px;

            &:hover {
                background: #B1CAFF;
                border-radius: 0px 5px 5px 0px;
            }

            &.active {
                background: #6091F8;
                border-radius: 0px 5px 5px 0px;

                a {
                    color: #fff;
                }
            }

            a {
                text-decoration: none;
                height: 63px;
                display: flex;
                align-items: center;
                padding-left: 40px;

                @media (max-width:1070px) {
                    padding-left: 25px;
                }
            }
        }
    }

    span {
        width: 30px;
        margin-right: 25px;
    }
}

