    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background: url(../img/select.svg) no-repeat 95% 50% #fff;
    border: 1px solid #DEE4F2;
    box-sizing: border-box;
    border-radius: 3px;
    padding: 0px 10px;
    width: 244px;
    height: 30px;

    @media (max-width:1170px) {
        width: 200px;
    }
}

.status {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: flex;

    span {
        border-radius: 50px;
        padding: 3px 8px;
        display: inline-block;
        color: #fff;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

.status__inwork {
    span {
        background: #FF6240;
    }
}

.status__assept {
    span {
        background: #FF9900;
    }
}

.status__init {
    span {
        background: #CA88FE;
    }
}

.ts {
    &__header {
        background: #FFFFFF;
        border-radius: 5px;
        padding: 16px 10px;
        margin-bottom: 20px;
        select {
            margin-left: 20px;
        }
    }

    &__vertical-view {
        width: 22px;
        position: relative;

        span {
            background: #DEE4F2;
            border-radius: 5px;
            height: 22px;
            width: 4px;
            display: block;
            margin: 0 auto;

            &:after,
            &:before {
                content: '';
                background: #DEE4F2;
                border-radius: 5px;
                height: 22px;
                width: 4px;
                display: block;
                position: absolute;
            }

            &:after {
                left: 0px;
            }

            &:before {
                right: 0px;
            }
        }

        &:hover,
        &.active {
            span {
                background: #6091F8;

                &:after,
                &:before {
                    background: #6091F8;
                }
            }
        }
    }

    &__horizontal-view {
        width: 22px;
        position: relative;
        display: flex;
        align-items: center;
        margin-left: 14px;

        span {
            background: #DEE4F2;
            border-radius: 5px;
            width: 22px;
            height: 4px;
            display: block;
            margin: 0 auto;

            &:after,
            &:before {
                content: '';
                background: #DEE4F2;
                border-radius: 5px;
                width: 22px;
                height: 4px;
                display: block;
                position: absolute;
            }

            &:after {
                top: 0px;
            }

            &:before {
                bottom: 0px;
            }
        }

        &:hover,
        &.active {
            span {
                background: #6091F8;

                &:after,
                &:before {
                    background: #6091F8;
                }
            }
        }
    }

    &__view {
        margin-left: 28px
    }

    &__filter {
        padding: 12px 10px 15px 10px;
        background: #FFFFFF;
        border-radius: 5px;
        margin: 20px 0px;
    }

    &__filter__title {
        margin-right: 7px;
        height: 28px;
        line-height: 28px;
    }

    &__filter__col {
        margin-right: 20px;
        a {
            color: #6091F8;
            text-decoration: underline;
            &:hover {
                text-decoration: none;
            }
        }
        span {
            display: block;
            color: #706F6F;
            margin-bottom: 4px;
        }
    }

    &__list {
        background: #FFFFFF;
        border-radius: 5px;
        padding-bottom: 17px;

        .ts__table {
            border-collapse: collapse;
            width: 100%;
            text-align: left;
            display: block;

            a {
                color: #000000;
                text-decoration: none;
                transition: all .2s;
                position: relative;

                &:hover {
                    background: #FFFFFF;
                    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.12);

                    >div {
                        background: #fff !important;
                    }

                    &::after {
                        content: "";
                        position: absolute;
                        left: 0px;
                        height: 100%;
                        border-left: 2px solid #6091F8;
                        width: 2px;
                    }
                }

                &:active {
                    background: #F3F6FC;
                    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.12);

                    >div {
                        background: #F3F6FC !important;
                    }

                    &::after {
                        content: "";
                        position: absolute;
                        left: 0px;
                        height: 100%;
                        border-left: 2px solid #6091F8;
                        width: 2px;
                    }
                }
            }
        }

        .ts__table-row {
            display: flex;
        }

        .ts__table__item:first-child {
            .ts__table-row {
                display: flex;
                width: 100%;

                .ts__table-col {
                    color: #6091F8;
                    font-style: normal;
                    font-weight: normal;
                    padding: 20px 10px 11px 10px;

                    display: block;
                    width: 100%;

                    &:first-child {
                        padding-left: 20px;
                        max-width: 30%;

                        @media (max-width:1070px) {
                            max-width: 140px;
                            width: 25%;
                            min-width: 140px;
                        }
                    }

                    &:nth-child(2) {
                        max-width: 25%;
                    }

                    &:nth-child(3) {
                        max-width: 30%;
                    }

                    &:nth-child(4) {
                        max-width: 25%;
                    }
                }
            }

        }

        .ts__table__item {
            border-bottom: 1px solid #DEE5F2;

            .ts__table-row:not(:first-child) {




                .ts__table-col {
                    background: #F3F6FC;
                    border-bottom: 1px solid #DEE5F2;

                }

            }

            &:last-child {
                border-bottom: none;

            }

            .ts__table-row:last-child {
                .ts__table-col {
                    border-bottom: none !important;
                }
            }
        }

        .ts__table-col {
            padding: 9px 10px;
            width: 100%;

            &:first-child {
                padding-left: 20px;
                max-width: 30%;

                @media (max-width:1070px) {
                    max-width: 140px;
                    width: 140px;
                    min-width: 140px;
                }
            }

            &:nth-child(2) {
                max-width: 25%;
            }

            &:nth-child(3) {
                max-width: 30%;
            }

            &:nth-child(4) {
                max-width: 25%;
            }

            &:last-child {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
}

.task__col {
    background: #F9FAFD;
    border-radius: 5px;
    margin-right: 10px;
    max-width: 180px;
    width: 100%;
    overflow: hidden;
    min-height: calc(100vh - 325px);
    height: 100%;
    overflow-y: auto;

    &__header {
        padding: 21px 10px 15px 10px;
        border-bottom: 3px solid #f2f2f2;

        span {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;
            color: #6091F8;
        }
    }

    &__summ {
        font-size: 14px;
        line-height: 16px;
        margin: 10px 0px 0px 0px;
        color: #000000;
    }

    &__item {
        display: block;
        border-radius: 5px;
        background: #FFFFFF;
        border: 1px solid #DEE4F2;
        border-radius: 5px;
        margin: 10px;
        padding: 9px 10px 18px 10px;
        text-decoration: none;
        line-height: 140%;
        transition: all .3s;

        &:hover {
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.12);
        }
    }

    &__title {
        font-weight: 500;
        color: #000000;
    }
}

.task__init {
    .task__col__header {
        border-bottom: 3px solid #CA88FE;
    }

    .task__col__item {
        border-left: 3px solid #CA88FE;
    }
}

.task__submitted {
