        border-bottom: 3px solid #FF9900;
    }

    .task__col__item {
        border-left: 3px solid #FF9900;
    }
}

.task__col::-webkit-scrollbar {
    width: 0;
}


::-webkit-scrollbar {
    width: 0;
}

.slick-next {
    right: -60px;
    width: 93px;
    height: 93px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    transition: all 0.3s;
    z-index: 2;
    position: fixed;

    @media (max-width:1070px) {
        right: -70px;
    }

    &:hover {
        background: #08235C;
    }
}

.slick-next:before {
    content: '→';
    left: 10px;
    position: absolute;
    top: 40%;
}


.slick-prev {
    left: 180px;
    width: 93px;
    height: 93px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    transition: all 0.3s;
    z-index: 2;

    @media (max-width:1070px) {
        left: -70px;
    }

    &:hover {
        background: #08235C;
    }
}

.slick-prev:before {
    content: '←';
    right: 10px;
    position: absolute;
    top: 40%;
}

.c__row {
    display: flex;
    margin: 0px -10px 20px -10px;
}

.c__col-12 {
    width: 100%;
    margin: 0px 10px;
}

.c__col-6 {
    width: 50%;
    margin: 0px 10px;
}

.c__col-4 {
    width: 33.33%;
    margin: 0px 10px;
}

