    line-height: 14px;
    color: #706F6F;
    display: flex;
    align-items: center;
    text-decoration: none;
    margin-bottom: 5px;
    display: block;
    width: 100%;

    &:before {
        content: '';
        background: url(../img/to-back.svg) no-repeat;
        width: 8px;
        height: 7px;
        margin-right: 8px;
        position: relative;
        top: -1px;
        display: inline-block;
    }
}

.task-parent {
    font-size: 16px;
    line-height: 19px;
    color: #706F6F;

    a {
        color: #706F6F;
