    color: #FFFFFF;
    margin: 10px 0px 15px 0px;
    height: 20px;
    display: inline-flex;
    padding: 0px 10px;
    align-items: center;

    &.task__approved {
        background: #FF9900;

        &:before {
            content: '';
            display: inline-block;
            background: url(../img/gal.svg) no-repeat center #F27400;
            border-radius: 50px;
            margin-left: -10px;
            width: 21px;
            margin-right: 5px;
            height: 20px;
        }
    }
}

.task-money {
    margin: 15px 0px 5px 0px;
}

.task-money__row {
    margin-right: 20px;

    >span {
        display: inline-block;
        color: #706F6F;
        margin-bottom: 4px;
    }

    input {
        background: #FFFFFF;
        border: 1px solid #DEE4F2;
        border-radius: 3px;
        width: 200px;
        height: 30px;
        padding-left: 10px;
        padding-right: 50px;
    }

    .task-money__input {
