}

textarea {
    width: 100%;
    height: 90px;
    background: #FFFFFF;
    border: 1px solid #DEE4F2;
    border-radius: 3px;
    margin-bottom: 10px;
    padding: 10px;
    resize: vertical;
}

.file-list {
    margin-bottom: 10px;

    .file-item {
        font-size: 14px;
        line-height: 16px;
        color: #706F6F;
        margin: 2px 0px;
        text-decoration: none;

        &:before {
            content: '';
            background: url(../img/attach.svg) no-repeat;
            width: 9px;
            height: 10px;
            margin-right: 7px;
            display: inline-block;
        }


    }

    &__item {
        display: block;
        margin-bottom: 4px;

        a {
            &:first-child {
                margin-right: 8px;
            }

            &.file-delete {
                display: inline-block;
                margin-left: 8px;
                opacity: 0;
                color: #9FBAF4;
                visibility: hidden;
            }
        }

        &:hover {
            a {
                opacity: 1;
                visibility: visible;
            }
        }
    }
}

.file-attach {
    input {
        position: absolute;
        opacity: 0;
        visibility: hidden;
    }

    span {
        font-size: 12px;
        line-height: 14px;
        text-decoration-line: underline;
        color: #6091F8;
        display: inline-block;
        cursor: pointer;
    }
}

.task-client {
    &__head {
        border-bottom: 1px solid #DEE5F2;
        padding: 0px 10px 10px 10px;
        margin: 0px -10px;

        a {
            display: table;
            font-size: 16px;
            line-height: 19px;
            text-decoration-line: underline;
            color: #6091F8;
        }

        span {
            display: block;
            font-size: 14px;
            line-height: 16px;
            color: #706F6F;
            margin-top: 6px;
        }
    }

    &__row {
        display: flex;
        margin-top: 10px;

        &::before {
            content: '';
            background: url(../img/user.svg) no-repeat;
            width: 10px;
            height: 11px;
            display: block;
            margin-right: 10px;
            margin-top: 1px;
        }
