        border-radius: 3px;
        width: 21px;
        height: 21px;
        display: block;
        margin-left: 4px;
        &.graph-view-1 {
            background-image: url(../img/graph-v-1.svg);
            background-repeat: no-repeat;
            background-position: center;
        }
        &.graph-view-2 {
            background-image: url(../img/graph-v-2.svg);
            background-repeat: no-repeat;
            background-position: center;
        }
        &:hover {
            background-color: #6091F8;
        }
        &.active {
            background-color: #6091F8;
        }
    }
}

.debet-more {
    grid-template-columns: repeat(3, 1fr);
    display: grid;
}

.c__table__row__dop {
    margin-bottom: 10px;
    .debet-more {
        border-left: 1px solid #ABB7D0;
        padding-left: 10px;
        margin-top: 0px !important;
        margin-left: 15px;
    }
}

.ts__list.ts__list__clients {
    .ts__table-col:nth-child(4), .ts__table__item:first-child .ts__table-row .ts__table-col:nth-child(4) {
        max-width: 200px;
    }
    .ts__table-col:first-child, .ts__table__item:first-child .ts__table-row .ts__table-col:first-child {
        max-width: 200px;
    }
}

.btn.btn-trains {
    border: 1px solid #6091F8;
    border-radius: 3px;
    font-size: 14px;
    color: #6091F8;
    background-color: #fff;
    &.btn-refresh {
        background: url(../img/refresh.svg) no-repeat;
        background-position: 10px 9px;
        margin-left: 30px;
        padding: 7px 12px 7px 35px;
    }
    &:hover {
        background-color: rgba(233, 240, 255, 0.9);
    }
}